<template lang="pug">
  #app(:class="browser.name")
    component(:is="layout")
</template>

<script>
  const {detect} = require('detect-browser');
  const browser = detect();
  import FrontLayout from "@/components/FrontLayout";
  import AuthLayout from "@/components/AuthLayout";
  import {mapGetters, mapActions} from "vuex";

  /**
   * STORE
   * app/javascript/store/modules/Layout.js
   */

  export default {
    name: "App",
    components: {
      FrontLayout,
      AuthLayout,
    },
    data() {
      return {
        browser
      }
    },
    computed: mapGetters(['layout']),
    created() {
      const {
        ready,
        check,
        user
      } = this.$auth;

      console.log({ check })

      ready(() => {
        if (check()) {
          this.setUser(user() || {})
        }
      })
    },
    methods: mapActions(['setUser']),
  };
</script>

