const state = {
  user: {
    id: null,
    first_name: null
  },

};

const mutations = {
  SET_UUID(state, payload) {
    state.uuid = payload;
  },
  SET_USER(state, payload) {
    state.user = payload;
  },
};

const getters = {
  user: (state) => state.user,
  uuid: (state) => state.uuid
}

const actions = {
  setUser({ commit }, payload) {
    commit('SET_USER', payload);
  },
  setUuid({ commit }, payload) {
    commit('SET_UUID', payload);
  },
};

export default {
  state,
  mutations,
  getters,
  actions,
};
