import Vue from "vue";
import VueResource from "vue-resource";
Vue.use(VueResource);

import { railsApi } from "../../config";

Vue.http.options.root = railsApi;

export const City = Vue.resource(`${railsApi}/cities{/id}`, {}, {
  analytics:    { method: "GET", url: `${railsApi}/cities{/name}/analytics` },
  costOfLiving: { method: "GET", url: `${railsApi}/cities{/name}/cost_of_living` },
  flightRoutes: { method: "GET", url: `${railsApi}/cities{/name}/flight_routes` },
  pricePerSquareMeter: { method: "GET", url: `${railsApi}/cities{/name}/price_per_square_meter` },
});
export const Country = Vue.resource(`${railsApi}/countries{/id}`, {}, {
  analyticsByName: { method: "GET", url: `${railsApi}/countries{/name}/analytics` },
  analyticsById: { method: "GET", url: `${railsApi}/countries{/id}/analytics` },
})
export const MailingSubscription = Vue.resource(`${railsApi}/mailing_subscriptions{/id}`);
export const Scraper = Vue.resource(`${railsApi}/scrapers{/id}`, {}, {
  batchLaunch: { method: "POST", url: `${railsApi}/scrapers{/id}/batch_launch` },
  launchNow: { method: "POST", url: `${railsApi}/scrapers{/id}/launch_now` },
})
export const User = Vue.resource(`${railsApi}/users{/id}`, {}, {
  authFacebook: { method: "POST", url: `${railsApi}/auth/facebook/token` },
  authGoogle: { method: "POST", url: `${railsApi}/auth/google/token` },
});

